___CSS_LOADER_EXPORT___.push([module.id, `/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

/* ----------------------------------------- */

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Form */
form {
  margin: auto;
  margin-top: 30px;
}

form > label:first-child {
  font-size: 28px;
}

#search {
  margin-left: 4px;
  margin-right: 4px;
}

#toggle {
  color: rgba(0, 0, 0, 0.2);
}

form > span {
  position: absolute;
  top: 75px;
  left: 492px;
  color: rgb(255, 57, 57);

  font-weight: lighter;
  font-size: 14px;
}

/* Info Div */
#info {
  margin-top: 60px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;

  position: absolute;
  top: 100px;
}

#info > div:first-child {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#time {
  font-weight: lighter;
}

#info > div:last-child {
  display: flex;

  gap: 40px;
}
#temperature,
#condition {
  font-weight: normal;
  font-size: 20px;
}

/* Loading */
#loading h1 {
  margin-top: 100px;
}
